export default {
    props: {
        labelCancel: {
            type: String,
            default: "取消"
        },
        labelConfirm: {
            type: String,
            default: "确定"
        }
    },
    methods: {
        cancel() {
            this.$emit("cancel");
        },
        save() {
            this.$emit("save");
        }
    }
};
